<template>
  <div class="container">
    <div class="row justify-content-center my-5">
      <div class="col-md-8">
        <h2 class="text-center">{{ exercise }} - Top 10 ({{ sex }})</h2>
        <table class="table table-striped table-bordered">
          <thead>
          <tr>
            <th>Name</th>
            <th>Result</th>
            <th>Weight</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(entry, index) in gymLeaderboardData" :key="index">
            <td>{{ entry[0] }}</td> <!-- Name -->
            <td>{{ entry[1] }}</td> <!-- Result -->
            <td>{{ entry[2] }}</td> <!-- Weight -->
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'GymLeaderboard',
  props: {
    exercise: String,
    sex: String,
    spreadsheetUrl: String
  },
  data() {
    return {
      gymLeaderboardData: []
    };
  },
  mounted() {
    this.fetchData();
    setInterval(() => {
      this.fetchData(); // Auto-refresh every 10 seconds
    }, 10000);
  },
  methods: {
    fetchData() {
      fetch(this.spreadsheetUrl)
          .then((response) => response.text())
          .then((data) => {
            const rows = data.split("\n").map((row) => row.split(","));
            this.gymLeaderboardData = rows.slice(1, 11); // Take top 10 rows
          })
          .catch((error) => console.error("Error fetching data:", error));
    },
  },
};
</script>

<style scoped>
/* Custom styling (if needed) */
</style>
